import React from 'react';

const Titles = () => (
	<div>
		<h1 className="title-container__title">Weather Search </h1>
		<h3 className="title-container__subtitle">
			Find out the temperature <br /> and conditions in your city!
		</h3>
	</div>
);

export default Titles;



